.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
form{
  background-color:#6389d3;
  border: 5px solid #000;
  padding: 20px;
  width: fit-content;
  margin: 0 auto;

}

.multiply, .divide{
  background-color: #f2f2f2;
  border: 2px solid #000;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
}
.add{
  background-color: rgb(4, 255, 25);
  padding: 10px;
  margin: 10px;
  cursor: pointer;

}
.subtract{
  background-color: rgb(252, 17, 0);
  padding: 10px;
  margin: 10px;
  cursor: pointer;
}
.input {
  font-size: 20px;
  min-height: 40px;
  color: black;
}
*{
  background-color: rgb(142, 171, 190);
}
.Result{
  font-size: 30px;
  margin: 5px;
  padding: 25px;
  padding-bottom: 40px;
  width: fit-content;
  margin: 0 auto;
  background: transparent;
}
b{
  background-color: transparent;
}
ol{
  background-color:#6389d3;
  border: 5px solid #000;
  padding: 20px;
  width: 100vh;
  display: block;
  margin: 0 auto;
  list-style-type: none;
}
li{
font-size: 20px;
margin: 0px;
padding: 5px;
padding-bottom:10px;
margin: 0 auto;
background: transparent;
position: relative;
}
li::before {
    content: '\2022'; /* Use Unicode character for bullet (‚óè) */
    color: black; /* Set color for the custom marker */
    font-size: 20px; /* Adjust size of the marker */
    position: absolute;
    left: 0; /* Position the marker at the beginning of the equation item */
    top: 50%; /* Align the marker vertically */
    transform: translateY(-50%); /* Adjust vertical alignment */
  }
.h2 {
  list-style-type: none; /* Remove default list item marker */
}
.clear{
  background-color: rgb(0, 0, 0);
  color: white;
  font-size: 30px;
  border: 3px solid white;
}
.equals{
  background-color: rgb(244, 248, 0);
  color: rgb(0, 0, 0);
  border:black 3px solid;
  font-size: 30px;
  margin-right: 15px;
  width: 100px;
}